import React from 'react';

export type SlideEntity = {
    children: JSX.Element;
} & React.DetailedHTMLProps<React.HTMLAttributes<HTMLDivElement>, HTMLDivElement>

export const SlideEntity = ({ children, className, ...rest }: SlideEntity) => {
    return (
        <div className={className ? 'slider-item '.concat(className) : 'slider-item'} {...rest}>
            {children}
        </div>
    );
};
