import { Module } from '@gotecq/core';
import { HomePage, BrowsingLayoutModule } from './page';
import { Landing } from './components';

function setup(module: Module) {

    module.panel('landing', Landing);

    module.route({
        path: '/home',
        title: 'Blogle',
        exact: true,
        secure: false,
        component: HomePage,
    });
    module.route({
        path: '/home/',
        title: 'Blogle',
        exact: false,
        secure: false,
        component: BrowsingLayoutModule,
    });
}

export { setup };
