import { LayoutPanelComponent } from '@gotecq/layout';
import { PaginatedList, paginatedPropsBridge, PaginatedSectionProps } from '@gotecq/paginated';
import { ComposeHeader } from '@gotecq/s8-component';
import { TArticle } from '@root/models';
import React, { useState, useRef } from 'react';
import { PaginateItem } from './paginate-item';
import './style.scss';

export const ContentList = ({ layoutService }: LayoutPanelComponent) => {
    const [baseDataURl, setBaseDataURl] = useState('https://magazine-api-server.herokuapp.com/api/article?limit=200');
    const [searchTxt, setSearchTxt] = useState('');
    const tableRef = useRef(null);

    const handleFetchDataFinish = (data: TArticle[]) => {
        const paramsQ = layoutService.getParam('primary');
        if (paramsQ.articleId) return;
        const _paramsQ = { ...paramsQ, articleId: data[0]?._id };

        layoutService.setPrimary('list', _paramsQ).go();
    };

    const handleArticleActive = (data: TArticle) => {
        const paramsQ = layoutService.getParam('primary');
        const _paramsQ = { ...paramsQ, articleId: data._id };

        layoutService.setPrimary('list', _paramsQ).go();
    };

    const handleEmptyReset = () => {
        setSearchTxt('');
        setBaseDataURl('https://magazine-api-server.herokuapp.com/api/article?limit=200');
    };

    const CustomHeader = (props: PaginatedSectionProps<any>) => {
        const handleSearchClick = (param: string) => {
            if (param) {
                setSearchTxt(param);
                setBaseDataURl('https://magazine-api-server.herokuapp.com/api/article/filter?query=' + param);
            } else {
                setSearchTxt('');
                setBaseDataURl('https://magazine-api-server.herokuapp.com/api/article?limit=200');
            }
        };

        return (
            <ComposeHeader>
                <ComposeHeader.HeaderTitle
                    {...paginatedPropsBridge.title({
                        ...props,
                        /**
                         * Overide default props as param
                         */
                        // paginatedState: {
                        //     ...props.paginatedState,
                        //     paginationData: {
                        //         ...props.paginatedState.paginationData,
                        //         totalItem: props.paginatedState.dataList.length
                        //     },
                        // }
                    })}

                    /**
                     * Overide with <ComposeHeader.HeaderTitle/> props
                    */
                    title={props.paginatedState.dataList.length + ' articles'}
                />
                <ComposeHeader.HeaderItem right>
                    <ComposeHeader.ControlledHeaderSearchInput
                        {...paginatedPropsBridge.search(props)}
                        value={searchTxt}
                        onSearch={handleSearchClick} />
                </ComposeHeader.HeaderItem>
            </ComposeHeader>
        );
    };

    return (
        <div style={{ height: '100%' }}>
            <PaginatedList
                Header={CustomHeader}
                onQuerySuccess={handleFetchDataFinish}
                Row={PaginateItem}
                baseDataURL={baseDataURl}
                metadataURL={null}
                keyExtractor={(data) => data._id}
                onActive={handleArticleActive}
                ref={tableRef}
                onEmptyReset={handleEmptyReset}
            />
        </div>
    );
};
