import { Module } from '@gotecq/core';
import { HomePage, BrowsingLayoutModule } from './page';
import { ContentDetail, ContentList } from './components';

function setup(module: Module) {

    module.panel('list', ContentList);
    module.panel('detail', ContentDetail);

    module.route({
        path: '/content-list',
        title: 'Blogle',
        exact: true,
        secure: false,
        component: HomePage,
    });
    module.route({
        path: '/content-list/',
        title: 'Blogle',
        exact: false,
        secure: false,
        component: BrowsingLayoutModule,
    });
}

export { setup };
