import { LayoutPanelComponent } from '@gotecq/layout';
import { Loading } from '@gotecq/s8-component';
import { TArticle } from '@root/models';
import React, { useState, useEffect } from 'react';
import HTMLReactParser from 'html-react-parser';
import { QueryAPIFetcher } from '@root/access';
import './style.scss';

export const ContentDetail = ({ layoutService }: LayoutPanelComponent) => {
    const [loading, setLoading] = useState(true);
    const [article, setArticle] = useState<TArticle>();
    const paramsQ = layoutService.getParam('primary');

    useEffect(() => {
        setLoading(true);
        let isCancelled = false;
        const { articleId } = layoutService.getParam('primary');

        if (articleId) {
            QueryAPIFetcher.article.detail<TArticle>(articleId)
                .then(data => {
                    if (isCancelled === false) {
                        setArticle(data);
                        setLoading(false);
                    }
                })
                .catch(e => {
                    setLoading(false);
                });
        }

        return () => { isCancelled = true; };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [paramsQ?.articleId]);

    return (
        <div className='article-wrap'>
            {loading === true && <Loading.FullView />}
            <div className="article">
                <div className="article-heading">
                    <div className="article-heading-box">
                        <h1 className="article-heading-title">
                            {article?.title}
                        </h1>
                        <p className="article-heading-introduce">{article?.introduce}</p>
                    </div>
                    <img src={article?.thumbnail} alt={article?.thumbnail} className="article-heading-media" />
                </div><div className="article-content">
                    {article?.content && HTMLReactParser(article?.content)}
                </div>
            </div>
        </div>
    );
};
