import { createURLBuilder } from '@gotecq/access';
import { API_BASE_URL } from '@root/config';

const URLMaker = createURLBuilder({
    ARTICLE_URL: () => `${API_BASE_URL()}/article`
});

export const QueryAPI = {
    article: {
        all: () => URLMaker('ARTICLE_URL').build(),
        detail: (id: string) => URLMaker('ARTICLE_URL').paths([id]).build(),
        filter: (query: string) => URLMaker('ARTICLE_URL').paths(['filter']).params({ query }).build()
    }
};

export const CommandAPI = {
    article: {
        create: () => URLMaker('ARTICLE_URL').build(),
        delete: (id: string) => URLMaker('ARTICLE_URL').paths([id]).build(),
        update: (id: string) => URLMaker('ARTICLE_URL').paths([id]).build(),
    }
};