import React from 'react';

export const PaginateItem = (props) => {
    const { data, onActive, active, } = props;
    const { _id, title, introduce, thumbnail } = data;

    return (
        <div onClick={() => {
            onActive(data);
        }} key={_id} className={active ? 'post-paginate-item active' : 'post-paginate-item'}>
            < div className="item-media" >
                <img src={thumbnail} alt={''} className='item-media-img' />
            </ div>
            <div className="item-content">
                <div className="item-content-title truncate">{title}</div>
                <p className="item-content-text">{introduce} </p>
            </div>
        </div >
    );
};
