import React from 'react';
import { ConnectedRouter } from 'connected-react-router';
import { Provider } from 'react-redux';
import { Switch, Route, Redirect } from 'react-router';
import { LayoutManager, DefaultURLEncoder } from '@gotecq/layout';
import { PageError404, Module, BaseApplication, AppBoundary, AppWrapper, NavBar } from '@gotecq/core';
import { configStoreAsync, EntityModule } from '@gotecq/state';
import { createBrowserHistory } from 'history';

import { loadAsyncConfig } from './config';
import { AppNavBar } from './component';
import './style/app.scss';

const InstalledModule = {
    'auth': require('@gotecq/core'),
    'content-list': require('./modules/content'),
    'home': require('./modules/home'),
};

export default class RootApplication extends BaseApplication {
    layoutManager: LayoutManager;
    constructor(props) {
        super(props);
        this.layoutManager = new LayoutManager(new DefaultURLEncoder());
    }
    getInstalledModule() {
        return InstalledModule;
    }
    getNavBar() {
        return <NavBar />;
    }
    async initiate() {
        // Setup begin
        this.setState({ loading: true });

        await loadAsyncConfig('app', process.env.PUBLIC_URL + '/environment.json', true);

        this.setupAnalysisAndMonitor();

        // Setup module
        this.setupModule();

        // Setup router
        this.history = createBrowserHistory({
            basename: '',
        });

        // Setup redux state
        this.store = await configStoreAsync(
            this.history,
            this.moduleRegistry.reducers(),
            this.moduleRegistry.sagas(),
        );

        this.layoutManager.setDispatchFunction(this.store.dispatch);

        // Setup end
        this.setState({ loading: false }, () => {
            this.initAppDone();
        });
    }
    setupModuleDone() {
        const entityModule = new Module();
        EntityModule.setup(entityModule, this.moduleRegistry.entities());
        this.moduleRegistry.register('@entity', entityModule);
        this.layoutManager.setComponentGetter((module: string, key: string) => {
            return this.moduleRegistry.getPanel(module, key);
        });
    }
    renderApp() {
        return (
            <AppBoundary>
                <Provider store={this.store}>
                    <ConnectedRouter history={this.history}>
                        <AppWrapper
                            layoutManager={this.layoutManager}
                            navbar={AppNavBar}
                            allRoutes={this.moduleRegistry.routes()}
                        >
                            <Switch>
                                <Route
                                    path="/"
                                    exact
                                    render={() => <Redirect
                                        to='/home'
                                    />}
                                />
                                {this.renderRoute()}
                                <Route component={PageError404} />
                            </Switch>
                        </AppWrapper>
                    </ConnectedRouter>
                </Provider>
            </AppBoundary>
        );
    }
    initAppDone() {
        console.log('Set up app done');
    }
    render() {
        const { loading } = this.state;
        if (loading) {
            return <div>Loading, please wait</div>;
        }
        return this.renderApp();
    }
}
