import { TArticle } from '@root/models';
import { AxiosResponse } from 'axios';
import { CommandAPI } from '.';
import { QueryAPI } from './api';
import { ArticleProfile, } from './index';

export const QueryAPIFetcher = {
    article: {
        all: async <T>(): Promise<T[]> => {
            try {
                const resp: AxiosResponse<T[]> = await ArticleProfile.Get(
                    QueryAPI.article.all()
                );
                return resp?.data;
            } catch (e) {
                return [];
            }
        },
        detail: async <T>(id: string): Promise<T> => {
            try {
                const resp: AxiosResponse<T> = await ArticleProfile.Get(
                    QueryAPI.article.detail(id)
                );
                return resp?.data;
            } catch (e) {
                return {} as T;
            }
        },
        filter: async <T>(query: string): Promise<T[]> => {
            try {
                const resp: AxiosResponse<T[]> = await ArticleProfile.Get(
                    QueryAPI.article.filter(query)
                );
                return resp?.data;
            } catch (e) {
                return [];
            }
        }
    }
};

export const CommandAPIFetch = {
    article: {
        create: async (article: TArticle): Promise<any> => {
            try {
                const resp: AxiosResponse<any> = await ArticleProfile.Post(
                    CommandAPI.article.create(), { data: article }
                );
                return resp?.data;
            } catch (e) {
                return e;
            }
        },
        delete: async (id: string): Promise<any> => {
            try {
                const resp: AxiosResponse<any> = await ArticleProfile.Delete(
                    CommandAPI.article.delete(id)
                );
                return resp?.data;
            } catch (e) {
                return e;
            }
        },
        update: async (id: string, article: TArticle): Promise<any> => {
            try {
                const resp: AxiosResponse = await ArticleProfile.Update(
                    CommandAPI.article.update(id), { data: article }
                );
                return resp?.data;
            } catch (e) {
                return e;
            }
        }
    }
};